import logo from "./logo.svg";
import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import StockData from "./Component/StockData";
import {
  Nav,
  Navbar,
  Button,
  Container,
  Form,
  Card,
  Offcanvas,
  Fade,
} from "react-bootstrap";
import { Routes, Route, Link } from "react-router-dom";
import React, { Component, useEffect, useState } from "react";
import Chart from "react-apexcharts";
import ApexChart from "react-apexcharts";
import StockChart from "./Component/StockChart";
import ApexChartDraw from "./Component/ApexChartDraw";
import MyPage from "./Component/MyPage.js";
import Login from "./Component/Login.js";
import RankPage from "./Component/RankPage.js";
import axios from "axios";

function App() {
  const [data, setData] = useState(null);
  //preSearch    
  const [preSearch, setPreSearch] = useState("");
  const [search, setSearch] = useState("005930");
  // StockData setSearchPrice state  
  const [searchPrice, setSearchPrice] = useState("");
  const [searchName, setSearchName] = useState("");
  const [error, setError] = useState(null);
  const [trade, setTrade] = useState("");
  const [tradeCheck, setTradeCheck] = useState(false);
  const [tradeMessage, setTradeMessage] = useState("");
  ///  
  const [number, setNumber] = useState(0);
  const [show, setShow] = useState(false);
  // check
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  //  id,pw,   
  const [id, setId] = useState("");
  const [pw, setPw] = useState("");
  const [nickName, setNickName] = useState("");

  //,    OffCanvas  
  const handleShowBuy = () => {
    setShow(true);
    setTrade("");
  };
  const handleShowSell = () => {
    setShow(true);
    setTrade("");
  };
  const handleClose = () => {
    setShow(false);
  };

  //MySQL   setSearch 
  useEffect(() => {
    //  parsedSearch 
    const parsedSearch = parseInt(preSearch);
    // 
    if (isNaN(parsedSearch)) {
      fetch(`/search?query=${preSearch}`)
        .then((response) => response.json())
        .then((data) => {
          if (data.length > 0 && data[0].hasOwnProperty("stock_code")) {
            const code = data[0].stock_code;
            //    search 
            setSearch(code);
            console.log(code);
          }
        });
    }
    setSearch(preSearch);
  }, [preSearch]);

  //ApexChart   
  useEffect(() => {
    //    30  
    fetch(`/getStockData30?search=${search}`)
      .then((response) => response.json())
      .then((data) => {
        if (data.output) {
          const transformedData = data.output.map((item) => ({
            x: item.stck_bsop_date,
            y: [item.stck_oprc, item.stck_hgpr, item.stck_lwpr, item.stck_clpr],
          }));
          setData(transformedData);
        } else {
          setError(data.error);
        }
      })
      .catch((error) => {
        console.error(error);
        setError(error.message);
      });
  }, [search]); // search . search    fetch

  return (
    <div>
      {isLoggedIn ? (
        <MainNav
          preSearch={preSearch}
          setPreSearch={setPreSearch}
          setIsLoggedIn={setIsLoggedIn}
          setId={setId}
          setPw={setPw}
        ></MainNav>
      ) : (
        ""
      )}
      <Routes>
        <Route
          path="/rank"
          element={
            isLoggedIn ? (
              <RankPage id={id}></RankPage>
            ) : (
              <Login
                setIsLoggedIn={setIsLoggedIn}
                setId={setId}
                setPw={setPw}
                setNickName={setNickName}
              ></Login>
            )
          }
        ></Route>
        <Route
          path="/"
          element={
            isLoggedIn ? (
              <div>
                <div>
                  <div className="row portfolio">
                    <div className="col-md-9">
                      <div style={{ height: "15vh" }}>
                        <StockData
                          search={search}
                          setSearch={setSearch}
                          handleShowBuy={handleShowBuy}
                          handleShowSell={handleShowSell}
                          setSearchPrice={setSearchPrice}
                          setSearchName={setSearchName}
                        ></StockData>
                      </div>
                      <div style={{ height: "70vh" }}>
                        <ApexChartDraw data={data}></ApexChartDraw>
                      </div>
                      <div class="container">
                        <div class="row">{/* <NewsCard></NewsCard> */}</div>
                      </div>
                    </div>
                    <div
                      className="col-md-3 portfolio-detail"
                      style={{ height: "90vh", overflow: "auto" }}
                    >
                      <PortfolioDetail
                        tradeCheck={tradeCheck}
                        id={id}
                        nickName={nickName}
                      ></PortfolioDetail>
                      {/* StockData  /  Offcanvas */}
                      <Offcanvas
                        show={show}
                        onHide={handleClose}
                        placement="end"
                      >
                        <Offcanvas.Header closeButton>
                          <Offcanvas.Title>
                            <h1
                              style={
                                trade == ""
                                  ? { color: "red" }
                                  : { color: "blue" }
                              }
                            >
                              {trade}
                            </h1>
                          </Offcanvas.Title>
                        </Offcanvas.Header>
                        <Offcanvas.Body>
                          <h2> </h2>
                          <h5>#    </h5>
                          <hr></hr>
                          <p></p>
                          <div
                            style={{
                              display: "flex",
                              position: "relative",
                              marginBottom: "10px",
                            }}
                          >
                            <div style={{ position: "relative", flex: 1 }}>
                              <input
                                type="text"
                                value={number}
                                onChange={(event) => {
                                  //    state 
                                  setNumber(event.target.value);
                                }}
                                style={{
                                  paddingRight: "30px",
                                  width: "100%",
                                  textAlign: "right",
                                }}
                              ></input>
                              <label
                                style={{
                                  position: "absolute",
                                  right: "10px",
                                  top: "50%",
                                  transform: "translateY(-50%)",
                                }}
                              >
                                
                              </label>
                            </div>
                            <div>
                              <button
                                onClick={() => {
                                  setNumber(parseInt(number) + 1);
                                }}
                              >
                                <svg
                                  xmlns="http://www.w3.org/2000/svg"
                                  width="16"
                                  height="16"
                                  fill="currentColor"
                                  class="bi bi-arrow-up"
                                  viewBox="0 0 16 16"
                                >
                                  <path
                                    fill-rule="evenodd"
                                    d="M8 15a.5.5 0 0 0 .5-.5V2.707l3.146 3.147a.5.5 0 0 0 .708-.708l-4-4a.5.5 0 0 0-.708 0l-4 4a.5.5 0 1 0 .708.708L7.5 2.707V14.5a.5.5 0 0 0 .5.5z"
                                  />
                                </svg>
                              </button>
                              <button
                                onClick={() => {
                                  if (parseInt(number) > 0) {
                                    setNumber(parseInt(number) - 1);
                                  }
                                }}
                              >
                                <svg
                                  xmlns="http://www.w3.org/2000/svg"
                                  width="16"
                                  height="16"
                                  fill="currentColor"
                                  class="bi bi-arrow-down"
                                  viewBox="0 0 16 16"
                                >
                                  <path
                                    fill-rule="evenodd"
                                    d="M8 1a.5.5 0 0 1 .5.5v11.793l3.146-3.147a.5.5 0 0 1 .708.708l-4 4a.5.5 0 0 1-.708 0l-4-4a.5.5 0 0 1 .708-.708L7.5 13.293V1.5A.5.5 0 0 1 8 1z"
                                  />
                                </svg>
                              </button>
                            </div>
                          </div>
                          {trade == "" ? (
                            // 
                            <button
                              style={{
                                width: "100%",
                                color: "#ed2926",
                                backgroundColor: "#f0d1cf",
                                border: "none",
                                borderRadius: "5px",
                                padding: "5px 10px",
                              }}
                              onClick={() => {
                                if (number <= 0) {
                                  setTradeMessage("  ");
                                  setTradeCheck(true);
                                  setTimeout(() => {
                                    setTradeCheck(false);
                                  }, 1100);
                                } else {
                                  fetch(
                                    `/buystock?number=${number}&id=${id}&searchPrice=${searchPrice}&searchName=${searchName}`
                                  )
                                    .then((response) => {
                                      if (!response.ok) {
                                        throw new Error(
                                          `HTTP error! status: ${response.status}`
                                        );
                                      }
                                      return response.text();
                                    })
                                    .then((message) => {
                                      console.log("Server response:", message);
                                      //    
                                      setTradeMessage(
                                        "  "
                                      );
                                      setTradeCheck(true);
                                      setTimeout(() => {
                                        setTradeCheck(false);
                                      }, 1100);
                                    })
                                    .catch((error) => {
                                      //      
                                      //  <-  
                                      console.error("Error:", error);
                                      setTradeMessage(" ");
                                      setTradeCheck(true);
                                      setTimeout(() => {
                                        setTradeCheck(false);
                                      }, 1100);
                                    });
                                }
                              }}
                              aria-controls="trade-check"
                              aria-expanded={tradeCheck}
                            >
                              {trade}
                            </button>
                          ) : (
                            // 
                            <button
                              style={{
                                width: "100%",
                                color: "#2679ed",
                                backgroundColor: "#d0dbef",
                                border: "none",
                                borderRadius: "5px",
                                padding: "5px 10px",
                              }}
                              onClick={() => {
                                if (number <= 0) {
                                  setTradeMessage("  ");
                                  setTradeCheck(true);
                                  setTimeout(() => {
                                    setTradeCheck(false);
                                  }, 1100);
                                } else {
                                  fetch(
                                    `/sellstock?number=${number}&id=${id}&searchPrice=${searchPrice}&searchName=${searchName}`
                                  )
                                    .then((response) => {
                                      if (!response.ok) {
                                        throw new Error(
                                          `HTTP error! status: ${response.status}`
                                        );
                                      }
                                      return response.text();
                                    })
                                    .then((message) => {
                                      console.log("Server response:", message);

                                      //    
                                      setTradeMessage(
                                        message ===
                                          "    "
                                          ? message
                                          : "  "
                                      );
                                      setTradeCheck(true);
                                      setTimeout(() => {
                                        setTradeCheck(false);
                                      }, 1100);
                                    })
                                    .catch((error) => {
                                      console.error("Error:", error);
                                      setTradeMessage(
                                        "  "
                                      );
                                      setTradeCheck(true);
                                      setTimeout(() => {
                                        setTradeCheck(false);
                                      }, 1100);
                                    });
                                }
                              }}
                              aria-controls="trade-check"
                              aria-expanded={tradeCheck}
                            >
                              {trade}
                            </button>
                          )}
                          <Fade in={tradeCheck}>
                            <h3
                              id="trade-check"
                              style={{
                                marginTop: "20px",
                                color: "#108229",
                                color:
                                  tradeMessage === "  " ||
                                  tradeMessage === " " ||
                                  tradeMessage ===
                                    "   " ||
                                  tradeMessage ===
                                    "    "
                                    ? "black"
                                    : "#4BA65F",
                                backgroundColor:
                                  tradeMessage === "  " ||
                                  tradeMessage === " " ||
                                  tradeMessage ===
                                    "   " ||
                                  tradeMessage ===
                                    "    "
                                    ? "#FCF311"
                                    : "#B3E0BD",
                                borderRadius: "10px",
                                textAlign: "center",
                                padding: "10px 20px",
                              }}
                            >
                              {tradeMessage}
                            </h3>
                          </Fade>
                        </Offcanvas.Body>
                      </Offcanvas>
                    </div>
                  </div>
                </div>
              </div>
            ) : (
              <Login
                setIsLoggedIn={setIsLoggedIn}
                setId={setId}
                setPw={setPw}
                setNickName={setNickName}
              ></Login>
            )
          }
        ></Route>
        <Route
          path="/mypage"
          element={
            isLoggedIn ? (
              <MyPage nickName={nickName} id={id}></MyPage>
            ) : (
              <Login
                setIsLoggedIn={setIsLoggedIn}
                setId={setId}
                setPw={setPw}
                setNickName={setNickName}
              ></Login>
            )
          }
        ></Route>
      </Routes>
    </div>
  );
}

export default App;

