function MainNav(props) {
  const [input, setInput] = useState("");

  const handleSubmit = (event) => {
    event.preventDefault();
    props.setPreSearch(input); // input  setSearch() 
  };
  const handleChange = (event) => {
    setInput(event.target.value); //    input  
  };

  return (
    <div>
      <Navbar
        bg="light"
        data-bs-theme="light"
        expand="lg"
        style={{ borderBottom: "1px solid" }}
      >
        <Container fluid style={{ marginLeft: "50px", marginRight: "10px" }}>
          <Link to="/" className="link-style">
            <img
              alt=""
              src="hen.png"
              width="30"
              height="30"
              className="d-inline-block align-top"
            />{" "}
            Chicken Stock
          </Link>
          <Navbar.Toggle aria-controls="navbarScroll" />

          <Navbar.Collapse id="navbarScroll">
            <Nav
              className="me-auto my-2 my-lg-0"
              style={{ maxHeight: "100px" }}
              navbarScroll
            >
              <Link to="/" className="link-style">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="20"
                  height="20"
                  fill="currentColor"
                  class="bi bi-house-fill"
                  viewBox="0 0 16 16"
                >
                  <path d="M8.707 1.5a1 1 0 0 0-1.414 0L.646 8.146a.5.5 0 0 0 .708.708L8 2.207l6.646 6.647a.5.5 0 0 0 .708-.708L13 5.793V2.5a.5.5 0 0 0-.5-.5h-1a.5.5 0 0 0-.5.5v1.293L8.707 1.5Z" />
                  <path d="m8 3.293 6 6V13.5a1.5 1.5 0 0 1-1.5 1.5h-9A1.5 1.5 0 0 1 2 13.5V9.293l6-6Z" />
                </svg>
                Home
              </Link>
              <Link to="/rank" className="link-style">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="20"
                  height="20"
                  fill="currentColor"
                  class="bi bi-award-fill"
                  viewBox="0 0 16 16"
                >
                  <path d="m8 0 1.669.864 1.858.282.842 1.68 1.337 1.32L13.4 6l.306 1.854-1.337 1.32-.842 1.68-1.858.282L8 12l-1.669-.864-1.858-.282-.842-1.68-1.337-1.32L2.6 6l-.306-1.854 1.337-1.32.842-1.68L6.331.864 8 0z" />
                  <path d="M4 11.794V16l4-1 4 1v-4.206l-2.018.306L8 13.126 6.018 12.1 4 11.794z" />
                </svg>
                Rank
              </Link>
            </Nav>
            <Form className="d-flex mx-auto" onSubmit={handleSubmit}>
              <Form.Control
                type="search"
                placeholder=" "
                className="me-2"
                aria-label="Search"
                onChange={handleChange}
              />
              <Button variant="outline-success" type="submit">
                Search
              </Button>
            </Form>
            <Link to="/mypage" className="link-style">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="25"
                height="25"
                fill="currentColor"
                class="bi bi-person-circle"
                viewBox="0 0 16 16"
                style={{ marginRight: "10px" }}
              >
                <path d="M11 6a3 3 0 1 1-6 0 3 3 0 0 1 6 0z" />
                <path
                  fill-rule="evenodd"
                  d="M0 8a8 8 0 1 1 16 0A8 8 0 0 1 0 8zm8-7a7 7 0 0 0-5.468 11.37C3.242 11.226 4.805 10 8 10s4.757 1.225 5.468 2.37A7 7 0 0 0 8 1z"
                />
              </svg>
              
            </Link>
            <button
              className="logout-btn"
              onClick={() => {
                props.setIsLoggedIn(false);
              }}
            >
              
            </button>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      {/*   */}
    </div>
  );
}

function PortfolioDetail(props) {
  //    array
  const [myStockData, setMyStockData] = useState(null);
  const [myCash, setMyCash] = useState("");
  //  /  detail 
  useEffect(() => {
    fetch(`/portfolio/detail?id=${props.id}`)
      .then((response) => response.json())
      .then((data) => setMyStockData(data));
    console.log(myStockData);
    fetch(`/portfolio/detail/cash?id=${props.id}`)
      .then((response) => response.json())
      .then((data) => setMyCash(data[0].cash.toString()));
  }, [props.tradeCheck, props.id]);
  return (
    <div>
      <div
        style={{
          textAlign: "center",
          backgroundColor: "#494B49",
          color: "white",
          borderRadius: "10px",
          padding: "5px 10px",
          marginTop: "10px",
          marginBottom: "10px",
        }}
      >
        <h3>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="22"
            height="22"
            fill="currentColor"
            class="bi bi-coin"
            viewBox="0 0 16 16"
            style={{ marginRight: "10px" }}
          >
            <path d="M5.5 9.511c.076.954.83 1.697 2.182 1.785V12h.6v-.709c1.4-.098 2.218-.846 2.218-1.932 0-.987-.626-1.496-1.745-1.76l-.473-.112V5.57c.6.068.982.396 1.074.85h1.052c-.076-.919-.864-1.638-2.126-1.716V4h-.6v.719c-1.195.117-2.01.836-2.01 1.853 0 .9.606 1.472 1.613 1.707l.397.098v2.034c-.615-.093-1.022-.43-1.114-.9H5.5zm2.177-2.166c-.59-.137-.91-.416-.91-.836 0-.47.345-.822.915-.925v1.76h-.005zm.692 1.193c.717.166 1.048.435 1.048.91 0 .542-.412.914-1.135.982V8.518l.087.02z" />
            <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z" />
            <path d="M8 13.5a5.5 5.5 0 1 1 0-11 5.5 5.5 0 0 1 0 11zm0 .5A6 6 0 1 0 8 2a6 6 0 0 0 0 12z" />
          </svg>
          {props.nickName} 
        </h3>
        <div
          style={{
            backgroundColor: "white",
            color: "black",
            borderRadius: "5px",
          }}
        >
          <span>(): </span>
          <span>{parseInt(myCash).toLocaleString()} </span>
        </div>
      </div>
      {myStockData && myStockData.length > 0 ? (
        myStockData.map((stock, index) => (
          <div
            key={index}
            className="1"
            style={{
              width: "100%",
              backgroundColor: "#E6E2C6",
              marginBottom: "15px",
              padding: "10px 15px",
            }}
          >
            <div className="2">
              <div className="1">
                <h4 style={{ color: "#000000" }}>{stock.stock_name}</h4>
                <span style={{ display: "inline-block", width: "80px" }}>
                   
                </span>
                <span>: {stock.quantity}</span>
              </div>
              <div className="2">
                <span style={{ display: "inline-block", width: "80px" }}>
                  
                </span>
                <span>
                  : {(stock.averagePrice * stock.quantity).toLocaleString()}
                </span>
              </div>
              <div className="3">
                <span style={{ display: "inline-block", width: "80px" }}>
                   
                </span>
                <span>: {stock.averagePrice.toLocaleString()}</span>
              </div>
            </div>
          </div>
        ))
      ) : (
        <div
          style={{
            textAlign: "center",
            backgroundColor: "#eee",
            borderRadius: "10px",
            padding: "10px 20px",
            marginTop: "30px",
          }}
        >
          <h4> !</h4>
          <h6>   </h6>
          <h6> , ,     .</h6>
        </div>
      )}
    </div>
  );
}
